import { Component, OnInit, AfterViewInit, OnChanges, Input, ViewChild, ElementRef, SimpleChanges } from '@angular/core';
import { AnimateDigitService } from '@services';

@Component({
  selector: 'app-no-of-application',
  templateUrl: './no-of-application.component.html',
  styleUrls: ['./no-of-application.component.scss']
})
export class NoOfApplicationComponent implements OnInit, AfterViewInit, OnChanges {

  // temporary static value start
  noOfS1Applications = Math.floor(Math.random() * 100000);
  noOfS2Applications = Math.floor(Math.random() * 100000);
  previousMonthValueS1: number = Math.ceil(Math.random() * 100) * (Math.round(Math.random()) ? 1 : -1);
  previousMonthValueS2: number = Math.ceil(Math.random() * 100) * (Math.round(Math.random()) ? 1 : -1);
  // temporary static value end

  duration: number = 1000;
  @Input() steps: number = 12;
  @ViewChild("noOfS1Applications") animateCountS1: ElementRef;
  @ViewChild("noOfS2Applications") animateCountS2: ElementRef;

  constructor(private animateDigitService: AnimateDigitService) { }

  ngOnInit(): void {
  }

  ngAfterViewInit() {
    if (this.noOfS1Applications || this.noOfS2Applications) {
      this.animateCount();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes["noOfS1Applications"] || changes["noOfS2Applications"]) {
      this.animateCount();
    }
  }

  animateCount() {
    this.animateDigitService.counterFunc(this.noOfS1Applications, this.duration, this.animateCountS1);
    this.animateDigitService.counterFunc(this.noOfS2Applications, this.duration, this.animateCountS2);
  }

}
